@use 'base/mixins' as m;

.galeria{
  @include m.grid(2, 2rem);

  @include m.desktop{ 
    @include m.grid(4, 2rem);
  }

  .imagen{
    img{
      height: 30rem;
      width: 30rem;
      object-fit: cover;
    }

    @include m.desktop{ 
      img{
        height: 100%;
        width: 100%;
      }
      &:nth-child(1){
        grid-row: 1 / 3;
      }

      &:nth-child(2){
        grid-column: 2 / 4;
      }

      &:nth-child(4){
        grid-row: 2 / 4;
      }

      &:nth-child(5){
        grid-column: 3 / 5;
      }

      &:nth-child(6){
        grid-column: 3 / 5;
      }

      &:nth-child(7){
        grid-row: 3 / 5;
      }

      &:nth-child(8){
        grid-column: 2 / 5;
        img{
          width: 94rem;
          height: 30rem;
        }
      }

      &:nth-child(9){
        grid-column: 1 / 3;
      }

      &:nth-child(10){
        grid-column: 3 / 5;
      }
    }
  }
}